import Image from "next/image";
import React from "react";
import { FaRegCalendarAlt, FaRegClock } from "react-icons/fa";
import BannerGrid from "./BannerGrid";
import Link from "next/link";
import NewsletterSubscription from "../NewsletterSubscription";

const MainBanner = ({ data, mainBanner }) => {
  if (!data) return null;
  const mainBannerData = mainBanner.slice(0, 1);

  return (
    <div className="main_width py-10">
      {/* Top Section Heading with Newsletter */}
      <div className="border-b border-gray-200 pb-4">
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4 mb-4">
          <div className="grid grid-cols-[14px_1fr] items-center gap-2">
            <div className="grid grid-cols-2 items-center">
              <div className="greenBg w-[8px] h-[48px]"></div>
              <div className="greenColorBg w-[8px] h-[48px]"></div>
            </div>
            <h2 className="font-semibold text-[32px]">Top story</h2>
          </div>

          {/* Newsletter Subscription - Compact Version */}
          <div className="lg:w-auto w-full">
            <NewsletterSubscription variant="compact" />
          </div>
        </div>
      </div>

      {/* Main Content Grid */}
      <div className="grid xl:grid-cols-[68%_1fr] lg:grid-cols-[60%_1fr] xl:gap-6 lg:gap-8 gap-10 mt-4">
        {/* Main Banner Card */}
        {mainBannerData.map((data, index) => {
          const updatedDate = data?.CreatedDate;
          const dateOnly = updatedDate
            ? updatedDate.split("T")[0]
            : "Unknown Date";
          const timeOnly = updatedDate
            ? updatedDate.split("T")[1]?.slice(0, 5)
            : "Unknown Time";
          return (
            <Link
              href={`/articles/${data.Slug || ""}`}
              key={index}
              className="block"
            >
              <div
                className="border border-gray-200 rounded-[20px]"
                key={index}
              >
                <div className="relative w-full xl:h-[500px] md:h-[400px] h-[250px] rounded-[20px] overflow-hidden">
                  <Image
                    src={data.FileUrlUpload}
                    alt={data.Title}
                    fill
                    sizes=" "
                    className="w-full h-full object-cover border"
                  />
                </div>
                <div className="lg:p-8 md:p-6 p-3">
                  <h1 className="lg:text-[40px] text-[#000] md:text-[35px] text-[20px] font-semibold lg:leading-[44px] line-clamp-4">
                    {data.Title}
                  </h1>
                  {data?.PostAuthorsList.map((data, index) => {
                    return (
                      <div className="mt-4 flex items-center gap-2" key={index}>
                        <div className="w-[40px] h-[40px] rounded-full border overflow-hidden">
                          <img
                            src={data.Photo || "/fav.png"}
                            alt={data.AuthorName}
                            className="w-full h-full object-contain"
                          />
                        </div>
                        <div className="font-medium">
                          <div>
                            <div className="font-medium">{data.AuthorName}</div>
                            <div className="">{data.UserName}</div>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                  <div className="mt-2">
                    <div className="flex items-center gap-4">
                      <div className="flex items-center gap-1">
                        <FaRegCalendarAlt className="text-gray-700" />
                        <span className="text-sm text-gray-800">
                          {dateOnly}
                        </span>
                      </div>
                      <div className="flex items-center gap-1">
                        <FaRegClock className="text-gray-700" />
                        <span className="text-sm text-gray-800">
                          {timeOnly}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Link>
          );
        })}

        {/* Grid Data */}
        <div>
          <BannerGrid gridData={data} />
        </div>
      </div>
    </div>
  );
};

export default MainBanner;
